import React from 'react';
import { Line } from 'react-chartjs-2';
import {
	CategoryScale,
	Chart,
	LinearScale,
	PointElement,
	LineElement
} from 'chart.js';
import './styles.css';
Chart.register(CategoryScale, LinearScale, PointElement, LineElement);
const DummyChart = () => {
	const lineChartData = {
		labels: ['October', 'November', 'December'],
		datasets: [
			{
				data: [8137119, 9431691, 10266674],
				label: 'Infected',
				borderColor: '#3333ff',
				backgroundColor: 'rgba(51, 51, 255, 0.5)',
				fill: true,
				lineTension: 0.5
			},
			{
				data: [1216410, 1371390, 1477380],
				label: 'Deaths',
				borderColor: '#ff3333',
				backgroundColor: 'rgba(255, 51, 51, 0.5)',
				fill: true,
				lineTension: 0.5
			}
		]
	};

	const options = {
		plugins: {
			legend: {
				display: true,
				position: 'top'
			}
		},
		scales: {
			x: {
				type: 'category',
				labels: ['October', 'November', 'December']
			}
		},
		responsive: true,
		maintainAspectRatio: false,
		layout: {
			padding: {
				left: 10,
				right: 10,
				top: 10,
				bottom: 10
			}
		}
	};

	return (
		<div>
			<Line data={lineChartData} options={options} />
		</div>
	);
};

export default DummyChart;
